import React, { } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setPeriode } from '../feature/periode.slice';
import dayjs from "dayjs"

const FilterPeriode = (props: any) => {
    const { onHomePage } = props
    const dispatch: any = useDispatch()
    const datas: any = useSelector((store: any) => store.periode)

    const defaultDate = dayjs().subtract(1, "year").format("YYYY")
    const isDefault: Boolean = datas.year === defaultDate

    const closeFilter: any = (ev: any) => {
        const element = ev.target.dataset.element
        switch (element) {
            case "search": dispatch(setPeriode({ search: 'tous' }))
                break;
            case "year": dispatch(setPeriode({ year: defaultDate }))
                break;
            case "option": dispatch(setPeriode({ option: "" }))
                break;
            case "level_value": dispatch(setPeriode({ level_value: "" }))
                break;
        }

    }
    return (
        <>
            {!onHomePage ? <div className='filter_periode'>
                <div className="title_bar">
                    <i className="fa fa-filter"></i>
                    <p>filtre</p>
                    {/* <i className="fa fa-caret-down" id="arrow"></i> */}
                </div>
                <div className="content">
                    {(datas.search !== "tous" && datas.search !== "") && <div className='item' style={{ animationDuration: "100ms" }} >
                        {datas.search} <i data-element="search" onClick={closeFilter} className='fa fa-close'></i>
                    </div>}
                    <div className={isDefault ? "item default" : "item"} style={{ animationDuration: "200ms" }} >
                        {datas.year}
                        {!isDefault ? <i
                            data-element="year"
                            onClick={closeFilter}
                            className='fa fa-refresh'></i> : <i className='fa fa-hashtag'></i>}
                    </div>
                    {datas.option && <div className='item' style={{ animationDuration: "300ms" }} >
                        {datas.option} <i data-element="option" onClick={closeFilter} className='fa fa-close'></i>
                    </div>}

                    {datas.level_value && <div className='item' style={{ animationDuration: "400ms" }} >
                        {datas.level_value} <i data-element="level_value" onClick={closeFilter} className='fa fa-close'></i>
                    </div>}
                </div>
            </div> :
                <div className='filter_periode on_mobile' style={{ marginTop: 20, borderColor: "transparent" }}>
                    <p>les donnes sont filtrer par : </p>
                    <div className="content" style={{ maxWidth: "100%", justifyContent: "center" }}>
                        {(datas.search !== "tous" && datas.search !== "") && <div className='item' style={{ animationDuration: "100ms" }} >
                            {datas.search} <i data-element="search" onClick={closeFilter} className='fa fa-close'></i>
                        </div>}
                        <div className={isDefault ? "item default" : "item"} style={{ animationDuration: "200ms" }} >
                            {datas.year}
                            {!isDefault ? <i
                                data-element="year"
                                onClick={closeFilter}
                                className='fa fa-refresh'></i> : <i className='fa fa-hashtag'></i>}
                        </div>
                        {datas.option !== "" && <div className='item' style={{ animationDuration: "300ms" }} >
                            {datas.option} <i data-element="option" onClick={closeFilter} className='fa fa-close'></i>
                        </div>}

                        {datas.level_value !== "" && <div className='item' style={{ animationDuration: "400ms" }} >
                            {datas.level_value} <i data-element="level_value" onClick={closeFilter} className='fa fa-close'></i>
                        </div>}
                    </div>
                </div>}
        </>
    );
};

export default FilterPeriode;