import { BookDatas } from "../_interface"
import Card from "./Card"
import SkeletonCard from "./SkeletonCard"

const UserBookCard = (props: any) => {
    const { setIsForm, books, isLoading, setEditBook, editBook } = props
    return <div className="user_book" style={{ position: 'relative' }}>
        <div className="header">
            <h3>Mes documents</h3>
            <button onClick={() => setIsForm(true)}><i className="fa fa-plus"></i> ajouter un document</button>
        </div>
        {isLoading ? [1, 2].map(() => <SkeletonCard />) : books?.map((book: BookDatas, key: any) => <Card setEditBook={setEditBook} editBook={editBook} book={book} key={key} />)}
    </div>
}
export default UserBookCard