import { useState } from 'react'
import Cookies from "js-cookie";
import BoxConfirm from "./BoxConfirm";
import Portal from "./Portal";
import { useDispatch } from 'react-redux';
import { disconnectUser } from '../feature/session.slice';
import { setNotification } from '../feature/notification.slice';



const LogOut = () => {
    const [confirm, setConfirm] = useState(false)
    const [showPopup, setShowPopup] = useState(false)
    const dispatch = useDispatch()

    const handleLogOut = () => {
        setShowPopup(true)
    }
    if (confirm) {
        Cookies.remove("token")
        Cookies.remove("userId")
        // window.location.reload()
        dispatch(disconnectUser({}))
        dispatch(setNotification({
            type: "info",
            message: "vous tes actuelement dconnecter",
            visible: true
        }))
    }

    return (
        <>
            <Portal>
                <BoxConfirm
                    visibility={showPopup}
                    setVisibility={setShowPopup}
                    message='voulez vous vraiment vous dconnecter ?'
                    setConfirm={setConfirm}
                />
            </Portal>
            <div className='log_out_btn' onClick={handleLogOut}>
                <i className='fa fa-sign-out'></i> <span>Se Dconnecter</span>
            </div>
        </>
    )
}

export default LogOut
