import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import axios from 'axios'
import TopBar from '../Components/TopBar';
import { BookDatas, UserDatas } from '../_interface';

function BookDetail(props: any) {
    const { id } = useParams()

    const [book, setBook] = useState<BookDatas>({
        _id: "",
        _idUser: "",
        thumbnail: "",
        pdfName: "",
        docxName: "",
        theme: "",
        option: "",
        level: 0,
        level_value: "",
        year: "",
        description: undefined
    })
    const [user, setUser] = useState<UserDatas>({
        _id: "",
        fullName: "",
        email: "",
        speciality: "",
        level: "",
        image: "",
        books: [],
    })

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/book/${id}`)
            .then((res: any) => {
                const { data } = res.data
                setBook(data)
            }).catch((error: any) => {
                console.log(error);
            })
    }, [id])
    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/users/${book._idUser}`)
            .then((res: any) => {
                const { data } = res.data
                setUser(data)
            }).catch((error: any) => {
                console.log(error);
            })
    }, [book._idUser])


    return (
        <>
            <TopBar currentPage="details" />
            <div id="book_details">

                <div
                    className="doc_preview animated animatedFadeInUp fadeInUp"
                    style={{ backgroundImage: `url(${process.env.REACT_APP_API_URL}/uploads/${book.thumbnail})` }}
                >
                    <a href={`${process.env.REACT_APP_API_URL}/uploads/${book.pdfName}`}>
                        <button>tlcharger</button>
                    </a>
                </div>
                <div
                    className="description animated animatedFadeInUp fadeInUp"
                    style={{ animationDelay: "200ms" }}
                >
                    <div className="user_detail">
                        <a
                            href='#lightbox'
                            className="image"
                            style={{
                                backgroundImage: `url("${process.env.REACT_APP_API_URL}/uploads/profile/${user.image || "default.png"}")`
                            }}
                        >&nbsp;</a>
                        <div className="text-content">
                            <h3 className="">
                                <a href='#lightbox'>{user.fullName}</a>
                            </h3>
                            <p><i className="fa fa-folder-o"></i> {user.speciality} </p>
                            <button className="option">
                                <i className='fa fa-phone'></i>
                                contacter
                            </button>

                        </div>
                    </div>

                    <div className="book">
                        <h2 className="theme">{book.theme}</h2>
                        <div className="info">
                            <span style={{ opacity: .7 }}><i className="fa fa-bookmark-o"></i> {book.option} </span>
                            <span><i className="fa fa-folder-o"></i> {book.level_value} </span>
                            <span style={{ opacity: .7 }}><i className="fa fa-calendar"></i> {book.year}</span>
                        </div>

                        <div className="text_box">
                            {book?.description}
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default BookDetail;