import React, { useState, useEffect } from 'react'
import SearchBar from '../Components/SearchBar'
import DropTags from '../Components/DropTags'
import axios from 'axios'
import FilterPeriode from '../Components/FilterPeriode'
import { Level } from '../_type'

const AsideBar = () => {
    const [toggle, setToggle] = useState(true);
    const [option, setOption] = useState([])
    const [year, setYear] = useState([])
    const [level, setLevel] = useState<Level[]>([
        { _id: "1", level: 2, value: "BTS" },
        { _id: "1", level: 3, value: "Licence" },
        { _id: "1", level: 5, value: "Master" }
    ])
    const [isLoading, setLoading] = useState(false)
    // const [thumb, setThumb] = useState(null) as any

    const handleScroll: any = (e: any) => {
        // console.log(e.currentTarget);
    }

    // define a function that sets min-height of my-element to window.innerHeight:
    const setHeight = () => {
        const menu = document.getElementById("aside_bar")
        if (menu) menu.style.height = window.innerHeight + "px"
    };

    // define mobile screen size:
    let deviceWidth = window.matchMedia("(max-width: 1024px)");

    if (deviceWidth.matches) {
        // set an event listener that detects when innerHeight changes:
        window.addEventListener("resize", setHeight);

        // call the function once to set initial height:
        setHeight();
    }



    useEffect(() => {
        setLoading(true)
        axios.get(`${process.env.REACT_APP_API_URL}/option`).then((res: any) => {
            const { data } = res.data
            setOption(data)
            setLoading(false)
        }).catch((error: any) => {
            console.log(error);
            setLoading(false)
        })

        axios.get(`${process.env.REACT_APP_API_URL}/year`).then((res: any) => {
            const { data } = res.data
            setYear(data.sort((a: any, b: any) => a.value - b.value))

        }).catch((error: any) => {
            console.log(error);
        })

        axios.get(`${process.env.REACT_APP_API_URL}/level`).then((res: any) => {
            const { data } = res.data
            setLevel(data)

        }).catch((error: any) => {
            console.log(error);
        })

        // const getThumbnail = async () => {
        //   const CoreControls = window.CoreControls;
        //   CoreControls.setWorkersPath('./webviewer/lib/core')
        // }

    }, [])

    return (
        <aside onClick={handleScroll} id='aside_bar' className={toggle ? "close" : ''}>
            <div className="mobile_top_aside" onClick={() => setToggle(!toggle)}>
                <i className={toggle ? "fa fa-angle-left" : "fa fa-angle-right"}></i>
            </div>
            <div className="content">
                <SearchBar />
                <FilterPeriode />
                <DropTags
                    libele="Cilcle"
                    datas={level}
                    type="level_value"
                    open={true}
                    icon="fa fa-tags"
                    isLoading={false}
                />
                <DropTags
                    libele="Annes"
                    datas={year}
                    type="year"
                    open={true}
                    icon="fa fa-calendar"
                    isLoading={isLoading}
                />
                <DropTags
                    libele="spcialit"
                    datas={option}
                    type="option"
                    open={true}
                    icon="fa fa-bookmark"
                    isLoading={isLoading}
                />
            </div>
        </aside>
    )
}

export default AsideBar
