import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import LogOut from './LogOut'
import { useSelector } from 'react-redux'

const SideMenu = () => {
    const screenSize: any = window.matchMedia('(max-width: 950px)').matches
    const [matches] = useState(window.matchMedia("(max-width: 600px)").matches)
    const [toggle, setToggle] = useState(screenSize);

    const { connected } = useSelector((store: any) => store.session)

    const autoToggle = (): (false | void) => matches && setToggle(!toggle)

    // define a function that sets min-height of my-element to window.innerHeight:
    const setHeight = () => {
        const menu = document.getElementById("side_menu")
        if (menu) menu.style.height = window.innerHeight + "px"
    };

    // define mobile screen size:
    let deviceWidth = window.matchMedia("(max-width: 1024px)");

    if (deviceWidth.matches) {
        // set an event listener that detects when innerHeight changes:
        window.addEventListener("resize", setHeight);

        // call the function once to set initial height:
        setHeight();
    }

    return (
        <>
            <nav
                id="side_menu"
                className={toggle ? "close" : ''}
                onTouchMove={() => setToggle(true)}
            >
                <div className="mobile_top_bar" onClick={() => setToggle(!toggle)}>
                    <i className={toggle ? "fa fa-bars" : "fa fa-close"}></i>
                </div>
                <div onTouchStart={() => setToggle(true)} className="overlay"></div>
                <nav className='nav_list' role="navigation">
                    <ul>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-home'></i> Accueil</NavLink>
                        </li>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/bookstore"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-home'></i> Librairy</NavLink>
                        </li>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/profile"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-user'></i> Mon Profil</NavLink>
                        </li>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/settings"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-cog'></i> Paramtre</NavLink>
                        </li>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/contact"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-address-book'></i> Contact</NavLink>
                        </li>
                        <li onClick={autoToggle}>
                            <NavLink
                                to="/about"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-info'></i> A propos</NavLink>
                        </li>
                        <li onClick={autoToggle} className={connected ? 'log-out-btn' : ""}>
                            {!connected && <NavLink
                                to="/sign"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            ><i className='fa fa-sign-in'></i> Se connecter</NavLink>}
                        </li>
                    </ul>
                    {connected && <LogOut />}

                </nav>
            </nav>
        </>
    )
}

export default SideMenu
