import React, { useEffect, useState } from 'react'
import axios from 'axios'
import TopBar from '../Components/TopBar'
import { useDispatch, useSelector } from 'react-redux'
import { Periode } from '../_type'
import { useNavigate } from 'react-router-dom'
import { setPeriode } from '../feature/periode.slice'
import LoadBox from '../Components/LoadBox'


const Home: React.FC = () => {
  const [specialities, setSpecialities] = useState<[]>([])
  const [isLoading, setLoading] = useState<boolean>(false)
  const navigate = useNavigate()

  const dispatch = useDispatch()

  const { option }: Periode = useSelector((store: any) => store.periode)

  const handleSpeciality = (e: any) => {
    const option: string = e.currentTarget.dataset.option

    dispatch(setPeriode({ option }))

    navigate('/bookstore')
  }

  useEffect(() => {
    setLoading(true)
    dispatch(setPeriode({ option: "" }))

    axios.get(`${process.env.REACT_APP_API_URL}/option`)
      .then((res: any) => {
        const { data } = res.data
        setSpecialities(data)
        setLoading(false)

      }).catch((error: any) => {
        console.log(error);
        setLoading(false)
      })

  }, [option, dispatch])

  return (
    <div className="home_page">
      <TopBar currentPage="accueil" />
      <h2>Choisissez une <span>Spcialit</span>! </h2>
      <div className="cards">
        {(!isLoading) ?
          specialities.map((speciality: any, key: number) =>
            <div
              style={{
                background: `linear-gradient(
                to top, 
                rgb(0 0 0 / .9), rgb(0 0 0 / 0)
              ), url(${process.env.PUBLIC_URL}images/option-${key + 1}.jpg)`
              }}
              className='card_option'
              onClick={handleSpeciality}
              data-option={speciality.value}
            >
              <div className="text_content">
                <p className='short'>{speciality.short}</p>
                <p>{speciality.value}</p>
              </div>
            </div>
          ) : <LoadBox isLoading={isLoading} size={100} />}
      </div>
    </div>

  )
}
export default Home