import axios from 'axios'
import React, { useState, useRef } from 'react'
import { useDispatch } from 'react-redux'
import { setNotification } from '../feature/notification.slice'
import { Level } from '../_type'
import LevelTag from './LevelTag'
import LoadBox from './LoadBox'
import Loader from './Loader'

const EditBookForm = (props: any) => {
    const { refrech, setRefrech, editBook, setEditBook } = props
    const [isChecked, setIsChecked] = useState<boolean>()

    const [pdf, setPdf] = useState<File | undefined>()
    const [docx, setDocx] = useState<File | undefined>()
    const refPdf = useRef<any>(null)
    const refDocx = useRef<any>(null)
    const [theme, setTheme] = useState<string>(editBook.theme)
    const [description, setDescription] = useState<string>(editBook.description)
    const [themeMessage, setThemeMessage] = useState<string>("")
    const [loaded, setLoaded] = useState<boolean>(false)
    const [level, setLevel] = useState<Level>({ _id: "", level: editBook.level, value: editBook.level_value })


    const dispatch = useDispatch()

    const handlePdfFile = (e: any): void => {
        const file = e.target.files[0]

        if (!file) return
        if (file.type === "application/pdf") {
            setPdf(file)
        } else {
            setPdf(undefined)
        }
    }

    const handleDocxFile = (e: any): void => {
        const file = e.target.files[0]
        if (!file) return
        const isDocx: boolean = file.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
        const isDoc: boolean = file.type === "application/msword";
        if (isDoc || isDocx) {
            setDocx(file)
        } else {
            setDocx(undefined)
        }
    }

    const handleSubmitBook = (e: any): void => {
        e.preventDefault()
        const isLevel: boolean = !!level.level && !!level.value
        const isFile = isChecked ? (!!pdf && !!docx) : true

        if (isLevel && theme.length > 15 && isFile) {
            setThemeMessage("");
            setLoaded(true)

            const formdata: FormData = new FormData(e.currentTarget);

            const isFile: Function = (): string => {
                if (isChecked) return "true"
                else return "false"
            }
            if (pdf === undefined || docx === undefined) {
                formdata.delete('pdf')
                formdata.delete('docx')
            }
            formdata.append('level_value', level.value)
            formdata.append('_idUser', editBook._idUser)
            formdata.append('year', editBook.year)
            formdata.append('level', level.level.toString())
            formdata.append('isFile', isFile())

            axios.patch(`${process.env.REACT_APP_API_URL}/book/${editBook._id}`, formdata)
                .then((res: any) => {
                    const { data } = res

                    setLoaded(false)
                    if (data.type === "success") {
                        dispatch(setNotification({
                            visible: true,
                            message: data.message,
                            type: data.type
                        }))
                        setEditBook(undefined)
                        setRefrech(!refrech)
                    } else {
                        console.log('erreur inconnue !');
                    }
                }).catch(error => {
                    console.log(error);

                    let message = error.response.data.message
                    let type = error.response.data.type
                    if (!message)
                        message = "500: Le seveur ne rpond pas! ressayer plus tard...";
                    if (!type)
                        type = "error";

                    dispatch(setNotification({
                        visible: true,
                        message: message,
                        type,
                    }))
                    setLoaded(false)
                })
        } else {
            if (theme.length === 0) setThemeMessage("le theme est obligatoire !")
            else if (theme.length < 15) setThemeMessage("le theme doit avoir plus de 15 caractrre !")
            if (theme.length >= 15) setThemeMessage("");

            !!pdf === false && setPdf(undefined)
            !!docx === false && setDocx(undefined)
        }
    }

    const handleCheck = (e: any): void => {
        const state = e.target.checked
        setIsChecked(state)
        setPdf(undefined)
        setDocx(undefined)
        refPdf.current.value = null;
        refDocx.current.value = null;
    }

    return (
        <form
            className="add_book_form"
            onSubmit={handleSubmitBook}
            encType="multipart/form-data"
        >
            <LoadBox isLoading={loaded} />
            <div className="files_upload">
                <div className="is_update_file">
                    <span><b>modifier les fichiers ?</b></span>
                    <input
                        type="checkbox"
                        hidden
                        name="is_update_file"
                        id='is_update'
                        onChange={handleCheck}
                    />
                    <label htmlFor="is_update">
                        <div className="commutateur"></div>
                    </label>
                </div>
                <div className={isChecked ? "" : "disable"}>
                    <span>Document Pdf <sup>*</sup></span>
                    <label className="lable_file" htmlFor="pdfFile">
                        <i style={{ color: "var(--danger)" }} className="fa fa-file-pdf-o"></i>
                        <input
                            type="file"
                            hidden
                            id="pdfFile"
                            accept='application/pdf'
                            onChange={handlePdfFile}
                            name="pdf"
                            ref={refPdf}
                        />
                        <p>{pdf?.name || ".pdf"}</p>
                    </label>
                    <span className="help_message">{(pdf === undefined && isChecked) && "selectionnez un document au format *PDF* s'il vous plait"}</span>
                    <span>Document Word <sup>*</sup></span>
                    <label className="lable_file" htmlFor="docxFile">
                        <i style={{ color: "var(--primary)" }} className="fa fa-file-word-o"></i>
                        <input
                            type="file"
                            hidden
                            id="docxFile"
                            accept='.doc, .docx'
                            onChange={handleDocxFile}
                            name='docx'
                            ref={refDocx}
                        />
                        <p>{docx?.name || ".doc, .docx"}</p>
                    </label>
                    <span className="help_message">{(docx === undefined && isChecked) && "selectionnez un document au format *WORD* s'il vous plait"}</span>
                </div>
            </div>
            <div className="field">
                <h3>information suplmentaire sur les document fournit</h3>
                <label htmlFor="">
                    <span>Theme <sup>*</sup></span>
                    <input
                        type="text"
                        name='theme'
                        onChange={(e: any) => setTheme(e.target.value)}
                        value={theme}
                        placeholder="le sujet de votre document"
                    />
                    <span className="help_message">{themeMessage}</span>
                </label>

                <LevelTag level={level} setLevel={setLevel} />

                <label htmlFor="">
                    <span>Description</span>
                    <textarea
                        value={description}
                        name="description"
                        onChange={(e: any) => setDescription(e.target.value)}
                        placeholder='dcrivez en quelques lignes le thme de votre document'
                    ></textarea>
                </label>
                <div className='add_book_btn'>
                    <button
                        className='cancel'
                        onClick={() => setEditBook(undefined)}
                        type="button"
                    >
                        <i className="fa fa-close"></i> annuler
                    </button>

                    <button
                        className='submit_book'
                        type="submit"
                    >
                        <Loader width={40} isLoading={loaded} />
                        {!loaded && "enregistrer"}
                    </button>
                </div>

            </div>
        </form>
    )
}

export default EditBookForm
