import axios from "axios"
import React, { useEffect, useState } from "react"
import UserCard from "../Components/UserCard"
import AddBookForm from "../Components/AddBookForm";
import TopBar from "../Components/TopBar";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { BookDatas, UserDatas } from "../_interface";
import EditBookForm from "../Components/EditBookForm";
import SkeletonUserCard from "../Components/SkeletonUserCard";
import UserBookCard from "../Components/UserBookCard";


const Profile = () => {
  const [user, setUser] = useState<UserDatas>({
    _id: "",
    fullName: "",
    email: "",
    speciality: "",
    contact: "",
    level: "",
    image: undefined,
  })
  const [refrech, setRefrech] = useState<boolean>(false)
  const [isForm, setIsForm] = useState<boolean>(false)
  const [editBook, setEditBook] = useState<BookDatas | undefined>(undefined)
  const [isLoading, setIsLoading] = useState<boolean>(true)
  const { userId, connected } = useSelector((store: any) => store.session)


  const navigate = useNavigate()

  // si l'utilisateur se deconnecte etant dja sur cette page
  if (!connected) navigate('/sign')


  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/users/${userId}`)
      .then((res: any) => {
        const { data } = res.data
        setUser(data)
        setIsLoading(false)
      }).catch((error: any) => {
        console.log(error);
        setIsLoading(false)
      })
  }, [userId, refrech])


  return (
    <div className="profile_page">
      <TopBar currentPage="profil utilisateur" />
      {isLoading ? <SkeletonUserCard /> : <UserCard user={user} setRefrech={setRefrech} />}
      {(!isForm && !editBook) && <UserBookCard
        setIsForm={setIsForm}
        books={user.books}
        isLoading={isLoading}
        setEditBook={setEditBook}
        editBook={editBook}
      />}

      {(isForm && !editBook) && <>
        <AddBookForm
          user={user}
          setIsForm={setIsForm}
          setRefrech={setRefrech}
        />
      </>}
      {editBook && <>
        <EditBookForm
          setRefrech={setRefrech}
          refrech={refrech}
          editBook={editBook}
          setEditBook={setEditBook}
        />
      </>}
    </div>
  )
}

export default Profile
